<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="ProjectName">
          <el-input v-model="ruleForm.ProjectName" :disabled="true">
            <template slot="prepend">案件名</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="ProjectCode">
          <el-input v-model="ruleForm.ProjectCode" :disabled="true">
            <template slot="prepend">案件CD</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="EstimateCode">
          <el-input v-model="ruleForm.EstimateCode" :disabled="true">
            <template slot="prepend">見積CD</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="OrderCode">
          <el-input v-model="ruleForm.OrderCode" :disabled="true">
            <template slot="prepend">注文CD</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="CustomerName">
          <el-input v-model="ruleForm.CustomerName">
            <template slot="prepend">得意先名</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="CustomerAddress">
          <el-input v-model="ruleForm.CustomerAddress">
            <template slot="prepend">得意先住所</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="Work">
          <el-input v-model="ruleForm.Work">
            <template slot="prepend">作業内容</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="PersonnelI">
          <el-input v-model="ruleForm.PersonnelI">
            <template slot="prepend">主任担当者(甲)</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="WorkTime">
          <el-input v-model="ruleForm.WorkTime">
            <template slot="prepend">作業期間</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="PersonnelII">
          <el-input v-model="ruleForm.PersonnelII">
            <template slot="prepend">主任担当者(乙)</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="Deliverables">
          <el-input v-model="ruleForm.Deliverables">
            <template slot="prepend">納入物</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="Commission">
          <el-input v-model="ruleForm.Commission">
            <template slot="prepend">委託料</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="DeliverableSpace">
          <el-input v-model="ruleForm.DeliverableSpace">
            <template slot="prepend">納入場所</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="PaymentDate">
          <el-input v-model="ruleForm.PaymentDate">
            <template slot="prepend">支払期日</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="Other">
          <el-input v-model="ruleForm.Other">
            <template slot="prepend">その他</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="AcceptanceConditions">
          <el-input v-model="ruleForm.AcceptanceConditions">
            <template slot="prepend">検収条件</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form-item label="" prop="Note">
          <el-input v-model="ruleForm.Note">
            <template slot="prepend">※(注)</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form-item>
          <el-button-group>
            <el-button type="warning" @click="cancelForm()">キャンセル</el-button>
            <el-button type="primary" @click="submitForm('ruleForm')">登録</el-button>
          </el-button-group>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
import {postOrderData, getOrderDataOne} from 'api/order'
import {getCustomerDataOne} from 'api/customers'

export default {
  name: 'OrderForm',
  components: {},
  props: {
    cOrderForm: {
      type: Object,
      default: function () {
        return {
          ProjectName: '',
          ProjectCode: '',
          CustomerName: '',
          EstimateCode: '',
          OrderCode: '',
          uuid: ''
        }
      }
    }
  },
  data() {
    return {
      ruleForm: {
        OrderCode: this.cOrderForm.OrderCode,
        ProjectName: this.cOrderForm.ProjectName,
        ProjectCode: this.cOrderForm.ProjectCode,
        EstimateCode: this.cOrderForm.EstimateCode,
        CustomerName: this.cOrderForm.CustomerName,
        CustomerAddress: '',
        Work: '',
        WorkTime: '',
        PersonnelI: '',
        PersonnelII: '',
        Deliverables: '',
        DeliverableSpace: '',
        Commission: '',
        PaymentDate: '',
        AcceptanceConditions: '',
        Other: '',
        Note: ''
      },
      rules: {
        CustomerName: [
          {required: true, message: '得意先名を入力してください', trigger: 'change'}
        ],
        CustomerAddress: [
          {required: true, message: '得意先住所を入力してください', trigger: 'blur'}
        ],
        Work: [
          {required: true, message: '作業内容を入力してください', trigger: 'blur'}
        ],
        WorkTime: [
          {required: true, message: '作業期間を入力してください', trigger: 'blur'}
        ],
        PersonnelI: [
          {required: true, message: '主任担当者(甲)を入力してください', trigger: 'blur'}
        ],
        PersonnelII: [
          {required: true, message: '主任担当者(乙)を入力してください', trigger: 'blur'}
        ],
        Deliverables: [
          {required: true, message: '成果物を入力してください', trigger: 'blur'}
        ],
        DeliverableSpace: [
          {required: true, message: '納入場所を入力してください', trigger: 'blur'}
        ],
        Commission: [
          {required: true, message: '委託料を入力してください', trigger: 'blur'}
        ],
        PaymentDate: [
          {required: true, message: '支払期日を入力してください', trigger: 'blur'}
        ],
        AcceptanceConditions: [
          {required: true, message: '検収条件を入力してください', trigger: 'blur'}
        ]
      }
    }
  },
  computed: {
    newDateTime() {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      var hours = date.getHours()
      var minutes = date.getMinutes()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = year + '-' + month + '-' + strDate + ' ' + hours + ':' + minutes

      return currentdate
    }
  },
  created: function () {
    this.getOrderDataOne()
    this.getCustomerDataOne()
  },
  methods: {
    postOrderData() {
      let values = {}
      values['OrderCode'] = this.ruleForm.OrderCode
      values['ProjectName'] = this.ruleForm.ProjectName
      values['ProjectCode'] = this.ruleForm.ProjectCode
      values['EstimateCode'] = this.ruleForm.EstimateCode
      values['CustomerName'] = this.ruleForm.CustomerName
      values['CustomerAddress'] = this.ruleForm.CustomerAddress
      values['Work'] = this.ruleForm.Work
      values['WorkTime'] = this.ruleForm.WorkTime
      values['PersonnelI'] = this.ruleForm.PersonnelI
      values['PersonnelII'] = this.ruleForm.PersonnelII
      values['Deliverables'] = this.ruleForm.Deliverables
      values['DeliverableSpace'] = this.ruleForm.DeliverableSpace
      values['Commission'] = this.ruleForm.Commission
      values['PaymentDate'] = this.ruleForm.PaymentDate
      values['AcceptanceConditions'] = this.ruleForm.AcceptanceConditions
      values['Other'] = this.ruleForm.Other
      values['Note'] = this.ruleForm.Note
      values['DateTime'] = this.newDateTime
      postOrderData(values).then(res => {
        if (res.status == 200) {
          this.$emit('submit')
          this.$message({
            showClose: true,
            message: '注文書データ登録・更新成功しました。',
            type: 'success'
          });
        } else {
          this.$emit('cancel')
          this.$message({
            showClose: true,
            message: '注文書データ登録・更新失敗しました。',
            type: 'error'
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getOrderDataOne() {
      let values = {}
      values['OrderCode'] = this.cOrderForm.OrderCode
      getOrderDataOne(values).then(res => {
        this.ruleForm.OrderCode = res.data.data[0].OrderCode
        this.ruleForm.ProjectName = res.data.data[0].ProjectName
        this.ruleForm.ProjectCode = res.data.data[0].ProjectCode
        this.ruleForm.EstimateCode = res.data.data[0].EstimateCode
        this.ruleForm.CustomerName = res.data.data[0].CustomerName
        this.ruleForm.CustomerAddress = res.data.data[0].CustomerAddress
        this.ruleForm.Work = res.data.data[0].Work
        this.ruleForm.WorkTime = res.data.data[0].WorkTime
        this.ruleForm.PersonnelI = res.data.data[0].PersonnelI
        this.ruleForm.PersonnelII = res.data.data[0].PersonnelII
        this.ruleForm.Deliverables = res.data.data[0].Deliverables
        this.ruleForm.DeliverableSpace = res.data.data[0].DeliverableSpace
        this.ruleForm.Commission = res.data.data[0].Commission
        this.ruleForm.PaymentDate = res.data.data[0].PaymentDate
        this.ruleForm.AcceptanceConditions = res.data.data[0].AcceptanceConditions
        this.ruleForm.Other = res.data.data[0].Other
        this.ruleForm.Note = res.data.data[0].Note
      }).catch(err => {
        console.log(err)
      })
    },
    getCustomerDataOne() {
      let values = {}
      values['CustomerCode'] = this.cOrderForm.CustomerCode
      getCustomerDataOne(values).then(res => {
        this.ruleForm.CustomerAddress = res.data.data[0].Address1 + ' ' + res.data.data[0].Address2 + ' ' + res.data.data[0].Address3
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
          this.postOrderData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelForm() {
      this.$emit('cancel')
    }
  }
}
</script>

