import {request} from 'network/request'

export function postOrderData (parameter) {
  return request({
    url: '/PostOrderData',
    method: 'post',
    data: parameter
  })
}

export function getOrderDataAll (parameter) {
  return request({
    url: '/GetOrderDataAll',
    method: 'post',
    data: parameter
  })
}

export function getOrderDataOne (parameter) {
  return request({
    url: '/GetOrderDataOne',
    method: 'post',
    data: parameter
  })
}
